export default {
  data: function data() {
    return {
      roomList: []
    };
  },
  methods: {
    check: function check(roomId) {
      var _this = this;

      this.$confirm("您确定要入住此宿舍吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(function () {
        _this.axios.get("/chooseRoom?id=" + roomId).then(function (res) {
          if (res.data.code == 1) {
            _this.$message.success("入住成功");

            _this.getUserInfo();
          }
        }).catch(function (err) {
          console.log(err);

          _this.$message.error("服务器无法连接");
        });
      }).catch(function () {
        _this.$message({
          type: "info",
          message: "操作已取消"
        });
      });
    },
    getUserInfo: function getUserInfo() {
      var _this2 = this;

      this.axios.get("/getUserInfo").then(function (res) {
        if (res.data.code == 1) {
          _this2.$store.commit("setValue", res.data.data);

          _this2.$router.push("/");
        }
      }).catch(function (err) {
        console.log(err);

        _this2.$message.error("服务器无法连接");
      });
    },
    newRoom: function newRoom() {
      var _this3 = this;

      this.axios.get("/newRoom").then(function (res) {
        if (res.data.code == 1) {
          _this3.$message.success("您的宿舍号为" + res.data.data.roomId);

          _this3.getUserInfo();
        }
      }).catch(function (err) {
        console.log(err);

        _this3.$message.error("服务器无法连接");
      });
    },
    getRoomList: function getRoomList() {
      var _this4 = this;

      this.axios.get("/getFilter").then(function (res) {
        if (res.data.code == 1) {
          _this4.roomList = res.data.data;
        }
      }).catch(function (err) {
        console.log(err);

        _this4.$message.error("服务器无法连接");
      });
    }
  },
  mounted: function mounted() {
    this.getRoomList();
  }
};