export default {
  data: function data() {
    return {
      form: {
        religion: this.$store.state.form.religion,
        quite: this.$store.state.form.quite,
        noise: this.$store.state.form.noise,
        sound: this.$store.state.form.sound,
        stay: this.$store.state.form.stay
      }
    };
  },
  methods: {
    finishClick: function finishClick() {
      var _this = this;

      for (var key in this.form) {
        if (this.form[key] === "") {
          this.$message.error('有未填项，请检查');
          return;
        }
      }

      var obj = {};

      for (var _key in this.form) {
        obj[_key] = this.form[_key].toString();
      }

      this.$store.commit("setFormValue", obj);
      this.$confirm('您确定要提交问卷吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(function () {
        _this.postSurvey();
      }).catch(function () {
        _this.$message({
          type: 'info',
          message: '操作已取消'
        });
      });
    },
    postSurvey: function postSurvey() {
      var _this2 = this;

      this.axios.post('/postSurvey', this.$store.state.form).then(function (res) {
        if (res.data.code == 1) {
          _this2.$message.success('提交成功');

          _this2.$router.push('/success');
        }
      }).catch(function (err) {
        console.log(err);

        _this2.$message.error('服务器无法连接');
      });
    },
    previous: function previous() {
      this.$router.push("/question/hobbies");
    }
  }
};