export default {
  data: function data() {
    return {
      active: 0
    };
  },
  watch: {
    $route: function $route(to) {
      var link = to.fullPath.split("/").pop();

      switch (link) {
        case "ready":
          this.active = 0;
          break;

        case "time":
          this.active = 1;
          break;

        case "hobbies":
          this.active = 2;
          break;

        case "habit":
          this.active = 3;
          break;
      }
    }
  },
  mounted: function mounted() {
    if (this.$store.getters.getRoom != "") {
      this.$router.push("/");
    } else {
      this.$router.push("/question");
    }
  }
};